ul {
  list-style: none;
}

.hero__container {
  max-width: 1200px;
  justify-content: space-around;
  align-items: center;
  column-gap: 30px;
  margin: auto;
  display: flex;
}

.hero-text__block {
  max-width: 500px;
  margin-left: 50px;
  line-height: 55px;
}

.hero__title {
  margin-bottom: 20px;
  font-family: Manrope;
  font-size: 44px;
  font-weight: 600;
}

.hero__text {
  font-family: Manrope;
  font-size: 20px;
}

.developer__container {
  max-width: 1200px;
  margin: auto;
}

.developer__list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: auto;
  display: flex;
}

.developer__item {
  width: 100%;
  width: calc(25% - 22.5px);
}

.developer__img {
  background-size: cover;
  border-radius: 50%;
  margin-bottom: 25px;
}

.developer__name, .developer__duties {
  text-align: center;
  margin-bottom: 15px;
}

.developer-social__list {
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 0;
  display: flex;
}

.developer-social__link {
  width: 44px;
  height: 44px;
  border: 2px solid #4440f6;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

